@import url(https://fonts.googleapis.com/css?family=Krub:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: Krub, source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-link {
  color: #61dafb;
}

.test {
  height: 100%;
  width: 100%;
  background-color: rgb(255, 110, 58);
  margin: auto;
  display: flex;
  margin-top: 0;
  position: relative;
}

.test2 {
  width: 100%;
  height: 40rem;
  background-color: rgb(240, 236, 231);
  align-self: center;
}

#all {
  margin-top: 0;
}

.t {
  font-family: "Krub", monospace;
  font-size: 15px;
}

.c {
  font-family: "Courier New", Courier, monospace;
  fill: #f58505;
  font-size: 18px;
  color: #f58505;
  text-shadow: black;
  font-weight: 800;
}

.cheat {
  margin-top: 3rem;
  color: rgb(22, 22, 22);
  width: 15rem;
  height: 3rem;
}

#cheat {
  background-color: rgb(255, 110, 58);
  text-align: center;
  justify-content: center;
}

.flex {
  display: flex;
  justify-content: center;
  background: rgb(255, 110, 58);
  width: 100%;
  flex-wrap: wrap;
}

.btn {
  font-family: "Krub", sans-serif;
  margin-top: 1.5rem;
  background-color: #ffecfd;
  font-weight: bold;
  color: rgb(76, 25, 124);
  width: 20rem;
  height: 3.5rem;
}

h3 {
  color: rgb(245, 245, 245);
  text-shadow: black;
  font-family: "Krub", sans-serif;
}

#btn_div {
  text-align: center;
}

.btn {
  margin-left: 3rem;
}


#top {
  margin-bottom: 0;
  justify-content: center;
  color: white;
  background-color: rgb(255, 110, 58);
  height: 4rem;
  display: flex;
  margin: 0;
  align-self: center;
  color: rgb(199, 224, 238);
}

.logo {
  width: 3em;
  height: 3em;
  justify-self: start;
  margin-top: 0.5em;
  margin-right: 2em;
}

.h2 {
  font-family: "Krub", sans-serif;
}

#about {
  height: 143rem;
  width: 100%;
  background-color: rgb(240, 236, 231);
  margin-top: 11.2em;
  font-family: "Krub", sans-serif;
  text-align: center;
}

.how {
  padding-top: 6.5em;
  font-size: 40px;
}

.p {
  line-height: 2em;
  font-size: 17px;
  padding: 20px;
}

#credit {
  margin-top: 18rem;
}

.mathpic {
  width: 80%;
}

