.flex {
  display: flex;
  justify-content: center;
  background: rgb(255, 110, 58);
  width: 100%;
  flex-wrap: wrap;
}

.btn {
  font-family: "Krub", sans-serif;
  margin-top: 1.5rem;
  background-color: #ffecfd;
  font-weight: bold;
  color: rgb(76, 25, 124);
  width: 20rem;
  height: 3.5rem;
}

h3 {
  color: rgb(245, 245, 245);
  text-shadow: black;
  font-family: "Krub", sans-serif;
}
